import { Injectable } from '@angular/core';
import { HttpClient } from "@angular/common/http";

import { timeout } from 'rxjs/operators';
import { updateBinding } from '@angular/core/src/render3/instructions';
import { promise } from 'selenium-webdriver';
import { of as observableOf, Observable } from 'rxjs';
import {BehaviorSubject} from "rxjs/Rx";
import {List} from "immutable"
import { asObservable } from './asObservable';
import { timer } from 'rxjs';
//import {asObservable} from "./asObservable";


@Injectable()
export class ConfigurationDataService {

  private _sourceConfig: BehaviorSubject<any[]> = new BehaviorSubject([]);
  private _targetConfig: BehaviorSubject<any[]> = new BehaviorSubject([]);
  private _command: BehaviorSubject<any[]> = new BehaviorSubject([]);

  private timerSubscription: any;

  constructor(private http: HttpClient){
    console.log("Constructor of: ConfigurationDataService");
    this.loadInitialData();
    const refresh = timer(0, 12000);
    this.timerSubscription = refresh.subscribe(val => {
      // initialize all data with  timer (not included in loadInitialData)
      this.getCommandData()
      .subscribe(
          res  => {
              let todos = res;
              this._command.next(todos);
          },
          err => {
            console.log("Error reading data from backend",err);
            alert("Error reading data from backend.\n\n"+err.message);
          })
    });
  }

  ngOnInit() {
 
  }    
  ngOnDestroy() {
    console.log("Destroying update data timer");
    this.timerSubscription.unsubscribe();
  }
  loadInitialData() {
    this.getSourcesData()
        .subscribe(
            res  => {
                let todos = res;
                this._sourceConfig.next(todos);
            },
            err => {
              console.log("Error reading data from backend",err);
              alert("Error reading data from backend.\n\n"+err.message);
            }
        );
    this.getTargetData()
        .subscribe(
            res  => {
                let todos = res;
                this._targetConfig.next(todos);
            },
            err => {
              console.log("Error reading data from backend",err);
              alert("Error reading data from backend.\n\n"+err.message);
            }
        );

  }

  get getSourceConfig() {
    return asObservable(this._sourceConfig);
  }

  setSourceConfig(sourceData:any):Observable<any> {
    console.log("setSourceConfig");
    let obs = this.setSourcesData(sourceData);

    obs.subscribe(
            res => {
                this._sourceConfig=sourceData;
                alert('The configuration data has been saved.');
            },
            err => {
              console.log("Error writing data to backend",err);
              alert("Error writing data to backend.\n\n"+err.message);
            }                
            );
    return obs;
  }


  get getTargetConfig() {
    return asObservable(this._targetConfig);
  }

  setTargetConfig(targetData:any):Observable<any> {
    console.log("setTargetConfig");
    let obs = this.setTargetData(targetData);

    obs.subscribe(
            res => {
                this._sourceConfig=targetData;
                alert('The configuration data has been saved.');
            },
            err => {
              console.log("Error writing data to backend",err);
              alert("Error writing data to backend.\n\n"+err.message);
            }                
            );
    return obs;
  }

  get getCommand() {
    return asObservable(this._command);
  }

  setCommand(commandData:any):Observable<any> {
    console.log("setCommand");
    let obs = this.setCommandData(commandData);

    obs.subscribe(
            res => {
                //this._command=commandData;  // todo: This will not work here and above....
                //alert('The command has been saved.');
            },
            err => {
              console.log("Error writing command to backend",err);
              alert("Error writing command to backend.\n\n"+err.message);
            }                
            );
    return obs;
  }

  // methods communicating directly wiht the backend
  private getSourcesData(){
    console.log("HIT private getSourcesData");
    return this.http.get<any[]>("/data/SourceConfiguration");
  }
  private setSourcesData(data:any):Observable<any>{
    console.log("HIT private setSourcesData");
    var headers = new Headers();
    headers.append('Content-Type', 'application/json; charset=utf-8');
    return this.http.post("/data/SourceConfiguration",JSON.stringify(data)).share();
  }
  private getTargetData(){
    console.log("HIT private getTargetData");
    return this.http.get<any[]>("/data/TargetConfiguration");
  }
  private setTargetData(data:any):Observable<any>{
    console.log("HIT private setTargetData");
    var headers = new Headers();
    headers.append('Content-Type', 'application/json; charset=utf-8');
    return this.http.post("/data/TargetConfiguration",JSON.stringify(data)).share();
  }
  private getCommandData(){
    console.log("HIT private getCommandData");
    return this.http.get<any[]>("/data/GetCommand");
  }
  private setCommandData(data:any):Observable<any>{
    console.log("HIT private setCommandData");
    var headers = new Headers();
    headers.append('Content-Type', 'application/json; charset=utf-8');
    return this.http.get<any[]>("/data/SetCommand/"+data.command).share();
  }
}
