import { Injectable } from '@angular/core';
import { HttpClient } from "@angular/common/http";
import { of as observableOf,  Observable, observable, } from 'rxjs';

import { map, filter, catchError, mergeMap } from 'rxjs/operators';
import { updateBinding } from '@angular/core/src/render3/instructions';
import { promise } from 'selenium-webdriver';
import { compileNgModuleFromRender2 } from '@angular/compiler/src/render3/r3_module_compiler';
import { Time } from '@angular/common';
import * as moment from 'moment';


@Injectable()
export  class StatisticDataService   {
  constructor(private http: HttpClient){
    console.log("Constructor of: StatisticDataService");
  }

  private data_OverviewSyncCountsWithNamesDistinct :any[]=[];
  private data_OverviewSyncCountsWithNamesDistinct_LastUpdate : number=0;
  private data_OverviewSyncCountsAggregated:any[]=[];

  private getOverviewSyncCountsWithNames(){

  }
  
  getTenantAllUsers(message:string):Observable<any[]>{
    // todo: Whats about caching?
    if (this.data_OverviewSyncCountsWithNamesDistinct_LastUpdate<moment.utc().add(-10,"seconds").unix())
    {
      // get new data from backend
      return this.http.get<any[]>("/data/OverviewSyncCountsWithNames").pipe(map(
        x=>{
          console.log("Yeah - getting data",message);
          this.data_OverviewSyncCountsWithNamesDistinct=[];  //clear to get new data
          this.data_OverviewSyncCountsAggregated=x;
          this.data_OverviewSyncCountsWithNamesDistinct_LastUpdate=moment.utc().unix();
          x.forEach(y=>{
            if (!this.data_OverviewSyncCountsWithNamesDistinct.find(arr=>arr.TenantName===y.TenantName))
            {
              this.data_OverviewSyncCountsWithNamesDistinct.push(y);
            }
          })
          return this.data_OverviewSyncCountsWithNamesDistinct;
        }
      ))
    }
    else {
      return observableOf(this.data_OverviewSyncCountsWithNamesDistinct);
      
    }
  }

  getTenantNumbers(message:string):Observable<any[]>{
    // todo: message
    return this.http.get<any[]>("/data/OverviewSyncCountsAggregated").pipe(map(
      x=>{
        console.log("Statistic.service - getting data tenant numbers");
        this.data_OverviewSyncCountsAggregated=[];  //clear to get new data
        //if (this.data_OverviewSyncCountsWithNamesDistinct_LastUpdate<new Date().setDate(new Date().getUTCDate-20))
        this.data_OverviewSyncCountsAggregated=x;
        return this.data_OverviewSyncCountsAggregated;
      }
    ))
  }

  getDistinctOpertations():Observable<any[]>{
    return this.http.get<any[]>("/data/InvitedUserLog").pipe(map(
      x=>{
        console.log("Statistic.service - getting data distinct opertations");
        return x;
      }
    ))
  }

  getProgressInfoData(): Observable<any[][]> {
    return this.http.get<any[]>("/data/LicenseInUse");
  }
}

